* {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
        Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-decoration: none;
}

.pageAll {
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px;
    height: 40px;
    max-width: 1024px;
    text-align: center;
}

.paginacion {
    width: 100%;
    height: 40px;
    background-color: white;
    display: flex;
    position: relative;
    overflow: hidden;
    justify-content: center;
    text-align: center;
}

.paginacion button {
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    width: 40px;
    height: 40px;
    text-align: center;
    color: black;
    font-size: 13px;
    font-weight: 700;
    transition: 0.2s linear;
    border-color: white;
    box-shadow: none;
}

.paginacion button:hover {
    background-color: orange;
    color: white;
}

.pagnumber {

    background-color: white;
    border-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    max-width: 1024px;
    text-align: center;
}